@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto", Arial, sans-serif;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto", Arial, sans-serif;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  display: grid;
  min-width: 320px;
  font-size: 15px;
  line-height: 30px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #1e1d26;
  grid-template-rows: auto 1fr auto;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0;
}
@media (min-width: 768px) {
  .container {
    padding: 0;
  }
}
@media (min-width: 1160px) {
  .container {
    padding: 0;
  }
}

.header {
  display: grid;
  padding-top: 121px;
  padding-bottom: 137px;
  background-color: #dceaf8;
  grid-template-columns: repeat(2, 1fr);
}

.logo {
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  color: #1e1d26;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 48px;
}

.navigation__toggle {
  display: none;
}

.navigation__link {
  font-size: 18px;
  text-decoration: none;
  color: #1e1d26;
}

.button {
  color: #fcf4f4;
  background-color: #b97024;
  font-size: 20px;
  font-weight: 500;
  border-radius: 30px;
  padding: 13px 51px;
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 48px;
  line-height: 74px;
  color: #b97024;
}

.presentation {
  display: grid;
  background-color: #dceaf8;
  grid-template-columns: 1fr 420px;
  padding-bottom: 100px;
}

.presentation__text {
  padding-top: 15px;
}

.presentation__title {
  font-size: 48px;
  line-height: 74px;
  font-weight: 700;
  margin: 0;
  width: 650px;
  padding-bottom: 56px;
}

.presentation__description {
  font-size: 30px;
  font-weight: 700;
}

.presentation__call {
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.presentation__image {
  background-color: #93badf;
  border-radius: 50%;
}

.call__title {
  font-size: 18px;
  font-weight: 400;
  width: 190px;
  display: block;
  padding-bottom: 20px;
}

@media (min-width: 768px) {
  .product-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: auto 0 0;
  }
}

.product-list__link {
  position: relative;
  display: block;
  padding: 24px 115px 31px 30px;
  font-weight: 700;
  line-height: 24px;
  font-size: 19px;
  color: #ffffff;
  text-decoration: none;
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .product-list__link {
    line-height: 30px;
    font-size: 20px;
    padding: 25px 115px 45px 36px;
  }
}
@media (min-width: 1150px) {
  .product-list__link {
    padding-bottom: 50px;
  }
}
@media (min-width: 768px) {
  .product-list__link:hover, .product-list__link:focus {
    background-color: #59d5bc;
    outline: none;
    transform: translateY(-5px);
  }
}

.product-list__item:nth-child(2n) .product-list__link {
  background-color: #56c3b2;
}
@media (min-width: 768px) {
  .product-list__item:nth-child(2n) .product-list__link:hover, .product-list__item:nth-child(2n) .product-list__link:focus {
    background-color: #46c1ae;
  }
}

.product-list__link span {
  display: block;
  max-width: 130px;
}

.product-list__link svg {
  position: absolute;
  right: 36px;
  bottom: 18px;
  opacity: 0.5;
  fill: #149080;
}
@media (min-width: 768px) {
  .product-list__link svg {
    bottom: 39px;
  }
}
@media (min-width: 1150px) {
  .product-list__link svg {
    bottom: 44px;
  }
}

.product-list__item--toys svg {
  right: 27px;
  bottom: 22px;
}
@media (min-width: 768px) {
  .product-list__item--toys svg {
    right: 33px;
    bottom: 39px;
  }
}
@media (min-width: 1150px) {
  .product-list__item--toys svg {
    right: 35px;
    bottom: 44px;
  }
}

.product-list__link:active svg {
  opacity: 0.3;
}

.promo {
  padding-top: 31px;
  padding-bottom: 42px;
  background-image: url("../img/svg/zigzag-desktop.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
}
@media (min-width: 768px) {
  .promo {
    padding-top: 41px;
    padding-bottom: 69px;
  }
}
@media (min-width: 1150px) {
  .promo {
    padding-top: 54px;
    padding-bottom: 12px;
    background: none;
  }
}

.promo__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 17px;
}
@media (min-width: 768px) {
  .promo__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 37px 42px;
  }
}
@media (min-width: 1150px) {
  .promo__container {
    position: relative;
    grid-template-columns: 1fr 525px;
    gap: 35px 60px;
    padding-bottom: 62px;
    padding-left: 225px;
  }
}
@media (min-width: 768px) {
  .promo__container::after {
    content: "";
    position: absolute;
    right: 50px;
    bottom: 0;
    left: 50px;
    z-index: 1;
    height: 23px;
    background-image: url("../img/svg/zigzag-desktop.svg");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
  }
}

@media (min-width: 768px) {
  .promo__title-container {
    display: grid;
    grid-template-columns: 1fr 125px;
    gap: 10px;
    grid-column: 1/-1;
    align-items: center;
  }
}
@media (min-width: 1150px) {
  .promo__title-container {
    grid-row: 1/span 1;
    padding-top: 61px;
  }
}

.promo__title {
  margin-bottom: 4px;
  text-align: start;
}
@media (min-width: 768px) {
  .promo__title {
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .promo__title {
    z-index: 1;
  }
}

.promo__type {
  display: block;
  font-weight: 700;
  line-height: 20px;
  font-size: 17px;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__type {
    line-height: 17px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .promo__type {
    position: absolute;
    top: 70px;
    left: 82px;
    max-width: 100px;
  }
}

.promo__description {
  margin: 0 0 5px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .promo__description {
    line-height: 30px;
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .promo__description {
    margin-bottom: 20px;
  }
}

.promo__specification {
  margin: 0;
}

@media (min-width: 768px) {
  .promo__cost-order {
    padding-top: 20px;
  }
}
@media (min-width: 1150px) {
  .promo__cost-order {
    padding-top: 74px;
  }
}

.promo__cost {
  display: block;
  margin-bottom: 24px;
  font-weight: 700;
  line-height: 24px;
  font-size: 17px;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__cost {
    margin-bottom: 32px;
    font-size: 20px;
    line-height: 24px;
  }
}

.promo__image {
  width: 100%;
  max-width: 350px;
  height: 315px;
  margin: 10px auto;
}
@media (min-width: 768px) {
  .promo__image {
    grid-column: span 1/-1;
    grid-row: 2/span 3;
    width: 334px;
    height: 404px;
    margin: 13px auto 0;
  }
}
@media (min-width: 1150px) {
  .promo__image {
    grid-row: 1/span 5;
    width: 525px;
    max-width: 100%;
    height: 636px;
    margin: 0;
  }
}

.promo__image img {
  display: block;
  width: 100%;
  height: 100%;
}

.specification__item {
  display: grid;
  grid-template-columns: 98px 1fr;
  line-height: 20px;
  border-bottom: 1px solid #ececec;
}

.specification__parameter,
.specification__property {
  margin: 0;
  padding: 7px 0 12px;
}
@media (min-width: 1150px) {
  .specification__parameter,
.specification__property {
    padding: 5px 0 14px;
  }
}

.about-us {
  padding-top: 76px;
  padding-bottom: 75px;
}

.about-us__container {
  display: grid;
  grid-template-columns: 640px 1fr;
}

.about-us__paragraph {
  font-size: 18px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 28px;
}

.about-us__text {
  padding-top: 26px;
}

.sertificates {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-between;
  gap: 37px 5px;
  padding: 0;
}

.sertificates__item {
  height: 175px;
}
.sertificates__item:nth-child(n+6) {
  height: 192px;
}

.sertificates__image {
  width: 100%;
  height: 100%;
}

.how-work {
  background-color: #ecebeb;
  padding-top: 75px;
  padding-bottom: 74px;
}

.how-work__list {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 4%;
}

.how-work__item {
  padding-top: 106px;
  font-size: 18px;
  line-height: 28px;
}

.services {
  padding-top: 75px;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  list-style: none;
  padding: 0;
}

.services__item {
  background-color: #aec9e4;
}

.about {
  padding: 31px 0;
}
@media (min-width: 768px) {
  .about {
    padding-top: 56px;
    padding-bottom: 54px;
  }
}

@media (min-width: 1150px) {
  .about__container {
    padding: 0 225px 0;
  }
}

.about__list {
  padding-top: 27px;
}
@media (min-width: 768px) {
  .about__list {
    padding-top: 41px;
    column-count: 2;
    column-gap: 0;
  }
}
@media (min-width: 1150px) {
  .about__list {
    padding-top: 39px;
  }
}

.about__item {
  margin-bottom: 28px;
  padding-top: 55px;
  font-weight: 700;
  line-height: 20px;
  font-size: 17px;
  color: #000000;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .about__item {
    max-width: 325px;
    margin-bottom: 54px;
    padding-top: 57px;
    font-size: 20px;
    line-height: 22px;
  }
}
.about__item:last-child {
  margin-bottom: 0;
}
.about__item--flower {
  background-image: url("../img/sprite.svg#icon-flower");
  background-position: top left 8px;
  background-size: 33px 38px;
}
.about__item--banknote {
  background-image: url("../img/sprite.svg#icon-banknote");
  background-position: top 1px left;
  background-size: 47px 27px;
}
.about__item--heart {
  background-image: url("../img/sprite.svg#icon-heart");
  background-position: top 6px left 7px;
  background-size: 34px 27px;
}
.about__item--knot {
  background-image: url("../img/sprite.svg#icon-knot");
  background-position: top 3px left 3px;
  background-size: 35px 34px;
}
@media (min-width: 768px) {
  .about__item--knot {
    background-position: top 4px left 3px;
  }
}
.about__item--rocket {
  background-image: url("../img/sprite.svg#icon-rocket");
  background-position: top 4px left -2px;
  background-size: 55px 23px;
}
.about__item--gift {
  background-image: url("../img/sprite.svg#icon-gift");
  background-position: top 2px left 3px;
  background-size: 33px 32px;
}

.reviews {
  position: relative;
  padding-top: 27px;
  padding-bottom: 51px;
  background-image: url("../img/svg/zigzag-line-desktop.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
}
@media (min-width: 768px) {
  .reviews {
    padding-top: 80px;
    padding-bottom: 102px;
    background-image: url("../img/svg/zigzag-line-desktop.svg"), url("../img/reviews-fon.jpg");
    background-repeat: no-repeat;
    background-position: center bottom, top left;
    background-size: contain, 350px 457px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .reviews {
    background-image: url("../img/svg/zigzag-line-desktop.svg"), url("../img/reviews-fon@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .reviews {
    padding-top: 8px;
    padding-bottom: 90px;
    background: none;
  }
}

@media (min-width: 768px) {
  .reviews__container {
    position: relative;
    display: grid;
    gap: 17px;
    grid-template-columns: auto 175px;
  }
}
@media (min-width: 1150px) {
  .reviews__container {
    position: relative;
    padding: 77px 50px 63px 225px;
    background-image: url("../img/reviews-fon.jpg");
    background-repeat: no-repeat;
    background-position: top left 50px;
    background-size: 350px 457px;
    gap: 14px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .reviews__container {
    background-image: url("../img/reviews-fon@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .reviews__container::after {
    content: "";
    position: absolute;
    right: 50px;
    bottom: -91px;
    left: 50px;
    z-index: 1;
    height: 23px;
    background-image: url("../img/svg/zigzag-line-desktop.svg");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
  }
}

.reviews__title {
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .reviews__title {
    align-self: end;
    margin: 0;
    grid-column: 1/2;
  }
}

.reviews__slider {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .reviews__slider {
    grid-column: 1/-1;
    grid-row: 2/3;
  }
}

.reviews__control {
  display: grid;
  margin-bottom: 38px;
  border: 1px solid #ececec;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .reviews__control {
    position: absolute;
    right: 50px;
    bottom: 40px;
    width: 175px;
    margin: 0;
    border: none;
  }
}
@media (min-width: 1150px) {
  .reviews__control {
    bottom: 106px;
  }
}

.reviews__control-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 49px;
  padding: 0;
  color: #000000;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .reviews__control-arrow {
    height: 35px;
  }
}
.reviews__control-arrow--next {
  border-right: 1px solid #ececec;
  transform: rotate(-180deg);
}
@media (min-width: 768px) {
  .reviews__control-arrow--next {
    border: none;
  }
}
.reviews__control-arrow:hover, .reviews__control-arrow:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.reviews__control-arrow:active {
  color: rgba(0, 0, 0, 0.3);
}

.reviews__control-arrow svg {
  width: 50px;
  height: 26px;
  fill: currentColor;
}

@media (min-width: 768px) {
  .reviews__button {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
@media (min-width: 768px) {
  .reviews__button {
    align-self: start;
  }
}

.review__text {
  margin: 0 0 33px;
}
@media (min-width: 768px) {
  .review__text {
    margin: 0 0 61px;
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .review__text {
    margin: 0 0 44px;
  }
}

@media (min-width: 768px) {
  .review__author {
    max-width: 500px;
  }
}

.review__author-name {
  display: block;
  margin-bottom: 3px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .review__author-name {
    font-size: 18px;
  }
}

.review__author-instagram {
  font-size: 17px;
  line-height: 30px;
}

.review__item {
  display: none;
}
@media (min-width: 768px) {
  .review__item {
    padding-top: 22px;
  }
}

.review__item--active {
  display: block;
}

.contacts {
  padding: 38px 0 39px;
}
@media (min-width: 768px) {
  .contacts {
    padding: 70px 0 58px;
    background-image: url("../img/sprite.svg#icon-flag");
    background-repeat: no-repeat;
    background-position: top 46px right 53px;
    background-size: 47px 81px;
  }
}
@media (min-width: 1150px) {
  .contacts {
    padding: 72px 0 105px;
    background: none;
  }
}

.contacts__container {
  padding: 0;
}
@media (min-width: 1150px) {
  .contacts__container {
