    grid-template-columns: 1fr auto;
  }
}

.contacts__text {
  padding: 0 29px 32px;
}
@media (min-width: 768px) {
  .contacts__text {
    padding: 0 50px 18px;
  }
}
@media (min-width: 1150px) {
  .contacts__text {
    padding: 80px 0 0 174px;
