    grid-row: 1/3;
  }
}

.contacts__button {
  width: 260px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contacts__button {
    width: 230px;
  }
}
@media (min-width: 1150px) {
  .contacts__button {
    margin-right: 84px;
  }
}

.contacts__property a {
  padding-bottom: 3px;
  color: #444444;
  text-decoration: none;
  border-bottom: 1px solid #62d1ba;
}
@media (min-width: 768px) {
  .contacts__property a {
    margin-right: 26px;
    padding-bottom: 4px;
  }
