@media (min-width: 1150px) {
  .contacts__property a {
    margin: 0;
    padding-bottom: 7px;
  }
}
.contacts__property a:hover, .contacts__property a:focus {
  color: #46c1ae;
  border-color: #46c1ae;
  outline: none;
}
.contacts__property a:active {
  border-color: rgba(70, 193, 174, 0.1);
}

.map {
  width: 100%;
  height: 454px;
  margin-bottom: 42px;
}
@media (min-width: 768px) {
  .map {
    height: 457px;
    margin-bottom: 55px;
  }
}
@media (min-width: 1150px) {
  .map {
    grid-column: -1/-2;
    grid-row: 1/span 2;
    width: 527px;
    margin: 0;
    margin-bottom: 0;
  }
}

.map iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.map__link {
  display: block;
  height: 100%;
}

.map__link img {
  display: block;
  width: 100%;
  height: 100%;
}

.no-js iframe {
  display: none;
}

.footer {
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
